@import '../variables';

.categories {
  @media (max-width: 1023px) {
    width: 100%;
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    @media (max-width: 1023px) {
      flex-wrap: nowrap;
      margin-right: -20px;
      padding-right: 20px;
      margin-left: -20px;
      padding-left: 20px;
      overflow-x: auto;
      max-width: calc(100% + 40px);
    }

    li {
      background-color: #f9f9f9;
      padding: 13px 30px;
      border-radius: 16px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();

      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }
}